.btn {
	background: $green;
	@include trans;
	font-size: 24px;
	@include rob-l;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	color: $white;
	min-width: 242px;
	padding: 12px 15px;
	&:hover {
		background: $orange; }
	&--big {
		min-width: 360px;
		@include rob;
		font-size: 36px; } }

