.prod-item {
	position: relative;
	&--small {
		background: $white;
		border-top: 3px solid $blue;
		border-bottom: 3px solid $blue;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0);
		flex-basis: 359px;
		flex-grow: 0;
		flex-shrink: 1;
		margin-right: 30px;
		margin-bottom: 56px;
		text-align: center;
		padding-bottom: 100px;
		min-height: 515px;
		@include trans;
		&:nth-of-type(3n) {
			margin-right: 0; }
		&:hover {
			box-shadow: 0 0 8px rgba(0, 0, 0, 0.14); }
		.prod-item__title {
			margin-bottom: 17px;
			padding: 0 5px; }
		.prod-item__text {
			@include rob-t;
			flex: 1;
			font-size: 24px;
			padding: 0 5px; }
		.prod-item__btn {
			position: absolute;
			bottom: 39px;
			left: 50%;
			transform: translateX(-50%); } }
	&__img {
		height: 241px;
		position: relative;
		margin-bottom: 28px;
		&:after {
			content: '';
			position: absolute;
			width: 41px;
			height: 4px;
			background-color: $blue;
			left: 50%;
			transform: translateX(-50%);
			bottom: -2px; }

		img {
			max-width: 100%;
			max-height: 241px; } }

	&__inner {}

	&__title {
		@include rob;
		color: $blue;
		font-size: 28px; }
	&__text {}

	&__price {} }
.prod-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-bottom: 26px; }
