.hero {
	padding-top: 152px;
	background-repeat: no-repeat;
	background-position: 50% 100%;
	background-size: cover;
	overflow: hidden;
	border-bottom: 5px solid $blue;
	margin-bottom: 85px;
	&__inner {
		text-align: center;
		max-width: 585px;
		position: relative; }
	&__title {
		margin-bottom: 75px; }
	&__btn {
		margin-bottom: 83px; }
	&__text {
		@include rob-b;
		color: $blue-d;
		font-size: 30px;
		border: 2px dashed $blue;
		padding: 14px 57px 14px 25px;
		border-radius: 18px;
		margin-left: 25px;
		max-width: 550px;
		span {
			display: block;
			margin-top: 12px;
			color: $green;
			font-size: 36px; } }
	&__img {
		position: absolute;
		z-index: 1;
		bottom: -64px;
		right: -183px; }
	.l {
		padding-bottom: 88px; } }
